/*!*---------slick slider--------------*!*/
.slick-slide {
  outline: none;
  border: none; }

.slick-next, .slick-prev {
  cursor: pointer;
  position: absolute;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  width: 30px;
  height: 30px;
  line-height: 30px;
  text-align: center;
  outline: none;
  -webkit-transition: 0.5s all ease;
  transition: 0.5s all ease;
  border: 0;
  z-index: 10;
  background: transparent; }
  .slick-next:hover, .slick-prev:hover {
    opacity: 0.6; }
  .slick-next:before, .slick-prev:before {
    font-family: FontAwesome;
    font-weight: bold;
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    top: 0;
    margin: auto;
    font-size: 16px;
    color: #28303d;
    -webkit-transition: 0.5s all ease;
    transition: 0.5s all ease; }

.slick-next {
  right: -15px; }
  .slick-next:before {
    content: '\f054'; }

.slick-prev {
  left: -15px; }
  .slick-prev:before {
    content: '\f053'; }

.slick-dots {
