  .slick-dots li {
    display: inline-block;
    width: 8px;
    height: 8px;
    border-radius: 50%;
    background: #28303d;
    border: 0;
    margin: 0 2px;
    cursor: pointer;
    -webkit-transition: 0.5s ease all;
    transition: 0.5s ease all;
    position: relative; }
    .slick-dots li button {
      background: none;
      outline: none;
      border: none;
      font-size: 0; }
    .slick-dots li.slick-active, .slick-dots li:hover {
      background-color: #28303d;
